<template>
    <div class='container'>
        <h2>{{post.title}}</h2>
        <div>
            <span>글쓴이 : </span>
            <span>{{post.id}}</span>
        </div>
        <div>
            <span>작성시간 : </span>
            <span>{{post.regdate}}</span>
        </div>
        <div>
            <p>
                {{post.contents}}
            </p>
        </div>
        <h3>코멘트</h3>
        <table class="table table-hover">
            <tr>
                <th>댓글번호</th>
                <th>글쓴이</th>
                <th>내용</th>
            </tr>
            <tr v-for="comment in comments" :key='comment.no'>
                <th>{{comment.comm_no}}</th>
                <th>{{comment.id}}</th>
                <th>{{comment.comments}}</th>
            </tr>
        </table>
        <button class="btn btn-default" @click='goBack'>목록으로</button>
        <button class="btn btn-default" @click='modifyPost'>수정</button>
        <button class="btn btn-default" @click='deletePost'>삭제</button>
        <form action="post">
            <!-- 로그인한 사용자 아이디 정보를 어디서 가져오지? -->
        </form>

    </div>
</template>

<script>

import axios from 'axios'
import router from '../assets/router.js'

export default {
    props:[
        "id",
    ],
    data() {
        return {
            post: {},
            comments: [],
        }
    },
    methods: {
        goBack(){
            router.push({
                path:'/board',
            })
        },
        modifyPost(){
            console.log(this.id);
            
        },
        deletePost(){

        }
    },
    mounted() {
        axios.get("http://localhost:8197/safefoodvue/api/searchBoard/"+ this.id)
            .then(response => {
                this.post = response.data;
            })
            .catch(error => {
                console.log(error);
            })
            .finally()
        axios.get("http://localhost:8197/safefoodvue/api/searchCommentsByNo/"+ this.id)
            .then(response => {
                this.comments = response.data;
            })
            .catch(error => {
                console.log(error);
            })
            .finally()
    },
}
</script>

